function Pontuacao() {
    return (
        <div className="explicacao-pontos">
            <p>O ranking foi realizado com base nas informaeses do campeonatos disponveis da IFBB no site da NPC News Online.</p>

            <p><strong>PONTUAO:</strong></p>

            <p>A pontuao que o atleta recebe  feita a partir da diviso entre quantos pontos valem o campeonato pela posio do atleta e a partir disso  somado todos os pontos.</p>

            <p><em>Exemplo: O Roelly Winklaar ficou em 6; lugar no Mr. Olympia de 2016 ento ele recebeu 16 pontos. (100 pontos / 6 lugar)</em></p>

            <p>Mr. Olympia: 100 pontos</p>

            <p>Arnold Sports Festival (Arnold Classic Ohio): 80 pontos</p>

            <p>Arnold Classic (Arnold Classic Brasil, Australia, etc...): 60 pontos</p>

            <p>Pittsburgh Pro, New York Pro, Tampa Pro, Toronto Pro, Japan Pro, Europa Pro Championships, Mr. Big Evolution Pro Portugal, Rising Phoenix: 60 pontos</p>

            <p>Chicago Pro, Romania Muscle Fest Pro, Legion Sports Fest Pro, Puerto Rico Pro, EVLS Prague Pro: 50 pontos</p>

            <p>Os demais eventos: 30 pontos</p>

        </div>
    )
}

export default Pontuacao;